.dark-theme {
  background-image: linear-gradient(
      315deg,
      rgb(0, 0, 0, 0.98) 47%,
      rgba(43, 43, 42, 0.92) 75%,
      rgb(17, 16, 16, 0.98) 100%
    ),
    url(../images/placeholder/b.jpg);
  background-size: cover;
  color: $primary-white-color;

  & .tui-ico-first,
  .tui-ico-prev,
  .tui-ico-next,
  .tui-ico-last,
  .tui-ico-ellip {
    filter: brightness(0) invert(1);
  }

  & .tui-page-btn {
    color: $primary-white-color;
    background-color: rgba(0, 0, 0, 0);
  }

  & .tui-is-disabled {
    filter: invert(58%) sepia(0%) saturate(7278%) hue-rotate(143deg) brightness(92%) contrast(96%);
  }

  & .tui-is-disabled:hover {
    background: #3b3a38;
  }

  & .tui-is-selected {
    background: $accent-color;
  }
}

.dark-footer {
  background-color: transparent;
}

.dark-modal {
  background-image: linear-gradient(
    45deg,
    rgb(25, 26, 26) 28%,
    rgb(62, 59, 59) 52%,
    rgb(148, 148, 147) 85%
  );
  color: rgb(94, 92, 92);
}

.dark-modal-text {
  color: rgba(0, 0, 0, 0.6);
}

.dark-spinner {
  background-color: rgb(88 86 86 / 83%);
  .cssload-inner.cssload-two {
    border-right: 7px solid rgba(255, 68, 11, 0.83);
  }
}

.toggle {
  position: absolute;
  bottom: 5%;
  left: 6%;
  width: 40px;

  .toggle-mask {
    width: 40px;
    height: 40px;
    overflow: hidden;
    cursor: pointer;

    .icon-wrap {
      padding: 10px;
      transition: all 300ms 400ms ease;

      &:last-child {
        transform: translatey(-40px);
      }
    }

    .icon {
      width: 20px;
      height: 20px;
      border-radius: 50%;
    }

    .active {
      transition-delay: 100ms;

      &:first-child {
        transform: translatey(40px);
      }

      &:last-child {
        transform: translatey(0);
      }
    }

    .sun {
      background-color: $primary-white-color;
    }

    .moon {
      background-color: $primary-white-color;
    }
  }

  .bar {
    width: 50%;
    height: 3px;
    margin: 0 auto;
    background-color: $primary-white-color;
    transition: all 300ms 100ms ease;

    &.dark {
      background-color: $primary-white-color;
    }
  }
}
